.footer {
    position: relative;
    color: #fff;
    padding: 50px 0 50px;
    background-color: #1A6EC1;
    
    .wave {
        position: absolute;
        top: -96px;
        left: 0;
        width: 100%;
        height: 100px;
        background: url("../images/wave.png");
        background-size: 1000px 100px;
      }
      
      .wave#wave1 {
        z-index: 1000;
        opacity: 0.8;
        bottom: 0;
        animation: animateWaves 6s linear infinite;
      }
      
      .wave#wave2 {
        opacity: 0.4;
        z-index: 999;
        bottom: 10px;
        animation: animate 7s linear infinite !important;
      }
      
      .wave#wave3 {
        z-index: 1000;
        opacity: 0.3;
        bottom: 15px;
        animation: animateWaves 4s linear infinite;
      }
      
      .wave#wave4 {
        z-index: 999;
        opacity: 0.7;
        bottom: 20px;
        animation: animate 5s linear infinite;
      }
      
      @keyframes animateWaves {
        0% {
          background-position-x: 1000px;
        }
        100% {
          background-positon-x: 0px;
        }
      }
      
      @keyframes animate {
        0% {
          background-position-x: -1000px;
        }
        100% {
          background-positon-x: 0px;
        }
      }
      
    
    &-logo {
        background-color: #ffffffe8;
        width: 230px;
        display: block;
        margin: 0 auto;
        border-radius: 5px;
    }
    &-links {
        display: flex;
        flex-direction: column;
        color: #fff;
        &__title {
            font-size: 32px;
            font-weight: 600;
            margin-bottom: 50px;
        }
        a {
            color: #fff;
            margin-bottom: 30px;
            font-size: 24px;
            font-weight: 500;
            transition: .3s;
            &:hover {
                color: #C1D6E9;
            }
        }
    }
}

@media screen and (max-width: 1400px) {
    .footer {
        color: #fff;
        &-logo {
            width: 200px;
            display: block;
            margin: 0 auto;
        }
        &-links {
            display: flex;
            flex-direction: column;
            color: #fff;
            &__title {
                font-size: 20px;
                font-weight: 600;
                margin-bottom: 30px;
            }
            a {
                color: #fff;
                margin-bottom: 20px;
                font-size: 16px;
                font-weight: 500;
                transition: .3s;
                &:hover {
                    color: #C1D6E9;
                }
            }
        }
    }
}

@media screen and (max-width: 1200px) {
    .footer {
        color: #fff;
        padding: 50px 0 30px;
        &-logo {
            width: 200px;
            display: block;
            margin: 0 auto;
        }
        &-links {
            display: flex;
            flex-direction: column;
            color: #fff;
            &__title {
                font-size: 18px;
                font-weight: 600;
                margin-bottom: 30px;
            }
            a {
                color: #fff;
                margin-bottom: 20px;
                font-size: 14px;
                font-weight: 500;
                transition: .3s;
                &:hover {
                    color: #C1D6E9;
                }
            }
        }
    }
}

@media screen and (max-width: 768px) {
    .footer {
        color: #fff;
        padding: 50px 0 20px;
        &-logo {
            width: 150px;
            display: block;
            margin: 0 auto;
        }
        &-links {
            margin-bottom: 10px;
            display: flex;
            flex-direction: column;
            color: #fff;
            &__title {
                font-size: 16px;
                font-weight: 600;
                margin-bottom: 30px;
            }
            a {
                color: #fff;
                margin-bottom: 20px;
                font-size: 12px;
                font-weight: 500;
                transition: .3s;
                &:hover {
                    color: #C1D6E9;
                }
            }
        }
    }
}